import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.css'

function FilterItem(props) {
  const [getIndex, setIndex] = React.useState();
  const [getItem, setItem] = React.useState();
  const [getSelection, setSelection] = React.useState({});
  const [getSort, setSort] = React.useState({ key: "name", name:true, len:true });

  if (props.Item !== getItem) {
    setIndex()
    setSelection({})
    setItem(props.Item)
  }

  const SelectClearItem = () => {
    setSelection({})
  }

  const SelectInvertItem = () => {
    let sel = {}
    Object.keys(getIndex).forEach((item) => {
      if (!getSelection[item]) {
        sel[item] = true
      }
    })
    setSelection(sel)
  }

  const SelectItem = (name) => {
    let sel = {
      ...getSelection
    }

    let v = sel[name]
    if (!v) {
      v = false;
    }
    sel[name] = v ? false : true

    setSelection(sel)
  }

  const SelectFilterItem = () => {
    let recs = []
    let names = []

    Object.keys(getIndex).forEach((item) => {
      if (getSelection[item]) {
        names.push(item)
        getIndex[item].forEach((id) => {
          recs.push(id)
        })
      }
    })

    if (names.length === 0) {
      return
    }

    names.sort();
    let name = names.join(",");

    if (props.FilterResult) {
      let results = {
        id: performance.now(),
        term: props.Item + ":" + name,
        records: recs
      }
      props.FilterResult(results)
      setIndex()
      setSelection({})
    }
  }

  let items = [];

  items.push(<span key={items.length}>
    [&nbsp;
    <a href="#" className="link-underline-primary me-2" onClick={SelectFilterItem}><b>Filter</b></a>
    <a href="#" className="link-underline-secondary me-2" onClick={SelectClearItem}>clear</a>
    <a href="#" className="link-underline-secondary" onClick={SelectInvertItem}>invert</a>
    &nbsp;]
  </span>)

  if (!getIndex) {
    let [propItem, propKind] = props.Item.split(":")
    let key = {}
    props.Results.forEach((item) => {
      let rec = props.Index.records[item]
      let text = rec[propItem]
      if (props.Item === "Time:Hour") {
        text = text.substring(0, 2) + ":mm:ss"
      }
      else if (props.Item === "Time:Min") {
        text = "hh:" + text.substring(3, 5) + ":ss"
      }

      if (!key[text]) {
        key[text] = [];
      }
      key[text].push(item)
    });

    setIndex(key)
  }
  else {
    items.push(<span className="mx-3">
      sort: [
      <span className={getSort.key === "name" ? "h6 mx-1" : "mx-1"}>
        { getSort.name && <a href="#" onClick={e => { setSort({ ...getSort, key: "name", name: false }); }}>name</a>}
        { !getSort.name && <a href="#" onClick={e => { setSort({ ...getSort, key: "name", name: true }); }}>NAME</a>}
      </span>
      <span className={getSort.key === "len" ? "h6 mx-1" : "mx-1"}>
        { getSort.len && <a href="#" onClick={e => { setSort({ ...getSort, key: "len", len: false }); }}>len</a>}
        { !getSort.len && <a href="#" onClick={e => { setSort({ ...getSort, key: "len", len: true }); }}>LEN</a>}
      </span>
      ]
    </span>)

    let unsorted = []
    Object.keys(getIndex).forEach(item => {
      unsorted.push({
        name: item,
        len: getIndex[item].length,
      })
    })

    let sorted = unsorted.sort((a, b) => {
      let order = []
      if (getSort.key === "name")
      {
        order.push("name")
        order.push("len")
      }
      else {
        order.push("len")
        order.push("name")
      }

      for (let i = 0; i < order.length; i++)
      {
        let key = order[i]
        if (key === "name") {
          let a1 = a.name.toLowerCase();
          let b1 = b.name.toLowerCase();
          let comp = a1.localeCompare(b1);

          if (comp > 0) {
            return getSort.name ? 1 : -1
          }
          else if (comp < 0) {
            return getSort.name ? -1 : 1
          }
        }
        else if (key === "len")
        {
          if (a.len > b.len) {
            return getSort.len ? 1 : -1
          }
          else if (a.len < b.len) {
            return getSort.len ? -1 : 1
          }
        }
      }

      return 0
    })

    items.push(<div></div>);

    sorted.forEach((item) => {
      items.push(
        <span key={items.length} className={"badge me-2" + (getSelection[item.name] ? " text-bg-info" : " text-bg-secondary")} role="button"
          onClick={() => SelectItem(item.name)}
        >{item.name ? item.name : <span className="badge text-bg-warning">null</span>
          } <span style={{ fontWeight: 300 }}>({item.len})</span>
        </span>)
    })
  }

  return (<>{items}</>);
}

export default FilterItem;
